import React from "react";

const ShareButtons = props => {
  const url = props.url;
  const text = props.text;
  const titleTag = text.split(" - ")[1];
  const hashtags = `zainsci,${titleTag.replace(" ", "")}`;
  return (
    <div className="share">
      <p>Share With Others</p>
      <div className="share__buttons">
        <div className="share__button">
          <a
            href={`https://twitter.com/intent/tweet?url=https://zainsci.github.io${url}&text=${text}\n&hashtags=${hashtags}`}
            target="_blank"
            rel="noreferrer"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="2"
              stroke-linecap="round"
              stroke-linejoin="round"
              class="feather feather-twitter"
            >
              <path d="M23 3a10.9 10.9 0 0 1-3.14 1.53 4.48 4.48 0 0 0-7.86 3v1A10.66 10.66 0 0 1 3 4s-4 9 5 13a11.64 11.64 0 0 1-7 2c9 5 20 0 20-11.5a4.5 4.5 0 0 0-.08-.83A7.72 7.72 0 0 0 23 3z" />
            </svg>
          </a>
        </div>
        <div className="share__button">
          <a
            href={`https://www.facebook.com/sharer/sharer.php?u=zainsci.github.io%2Fblog${url}`}
            target="_blank"
            rel="noreferrer"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="2"
              stroke-linecap="round"
              stroke-linejoin="round"
              class="feather feather-facebook"
            >
              <path d="M18 2h-3a5 5 0 0 0-5 5v3H7v4h3v8h4v-8h3l1-4h-4V7a1 1 0 0 1 1-1h3z"></path>
            </svg>
          </a>
        </div>
      </div>
    </div>
  );
};

export default ShareButtons;
